import React, { Component, Fragment } from 'react';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';
import _ from 'lodash';
import FieldGenerator from 'components/FormsV3/FieldGenerator';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';
import { validateFormValue, validateForm } from 'lib/validatorsV2';
import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import { sendEmailForm } from 'actions/insuranceActions';
import './Enquiry.scss';

const forms = [
  {
    size: 12,
    title: 'Email',
    slug: 'email',
    type: 'email',
    is_required: true,
  }, {
    size: 6,
    title: 'Full name',
    slug: 'name',
    type: 'text',
    is_required: true,
  }, {
    size: 6,
    title: 'Mobile number',
    slug: 'contact',
    type: 'number',
    is_required: true,
  }, {
    size: 12,
    title: 'Drop your enquiry here',
    slug: 'note',
    type: 'textarea',
    is_required: true,
  },
];

const ERROR_EXCEPTIONS = [
  'Field cannot be empty!',
];


class Enquiry extends Component {
  constructor(props) {
    super(props);

    this.state = {
      values: {},
      errors: {},
      groupError: '',
    };

    this.getError = this.getError.bind(this);
    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOnClose = this.handleOnClose.bind(this);
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const { values, errors } = this.state;
    const error = await validateFormValue(name, { type, value }, values, forms);

    const newState = {
      values: { ...values, [name]: { type, value } },
      errors: _.pickBy({ ...errors, [name]: error }, _.identity),
    };

    this.setState(newState);
  }

  getError() {
    const { values } = this.state;
    const fieldsToCheck = Array.from(forms, f => f.slug);
    const formErrors = validateForm(values, fieldsToCheck, forms);
    const keys = Object.keys(formErrors);
    if (keys.length > 0) {
      const error = formErrors[keys[0]] || null;
      return error || null;
    }

    return null;
  }

  async handleSubmit() {
    const { interest } = this.props;
    const { values } = this.state;

    const requestData = {
      form_category: interest,
      input_data: _.map(forms, f => ({
        name: f.title,
        value: values[f.slug].value,
        type: 'text',
      })),
    };
    requestData.input_data.push({
      name: 'Interests',
      value: interest || 'N/A',
      type: 'text',
    });

    try {
      const responseData = await sendEmailForm(requestData);

      if (responseData) {
        const { checkoutComplete } = this;
        this.setState({ groupError: '' }, () => checkoutComplete.openCheckoutComplete());
      } else {
        throw (responseData);
      }
    } catch (errors) {
      this.setState({ groupError: 'Request failed! Please try again later.' });
    }
  }

  handleOnClose() {
    const { onClose } = this.props;

    this.setState({
      values: {},
      errors: {},
      groupError: '',
    });

    if (onClose) {
      onClose();
    }
  }

  render() {
    const { isOpen } = this.props;
    const { values, groupError } = this.state;
    const error = this.getError();

    return (
      <Fragment>
        <ModalPopup
          showModal={isOpen}
          closeModal={() => this.handleOnClose()}
          className="partnership-enquiry-modal"
          backdropClassName="blue-modal-backdrop"
          header={<p>Submit enquiry</p>}
          body={
            <Fragment>
              <Row noGutters>
                {
                  _.map(forms, (form, index) => (
                    <FieldGenerator
                      key={`partnership-form-${index}`}
                      values={values}
                      fieldData={form}
                      onChangeValue={this.onChangeValue}
                      extraInfo={form.extra_info && <p>{form.extra_info}</p>}
                    />
                  ))
                }
              </Row>
              <p
                className="text-danger text-center"
                dangerouslySetInnerHTML={{ __html: (!_.includes(ERROR_EXCEPTIONS, error) && error) || groupError || '&nbsp;' }}
              />
            </Fragment>
          }
          footer={
            <div className="w-100 text-center">
              <Button
                variant="medium-flat-green"
                height={48}
                width={162}
                disabled={(!!error)}
                onClick={this.handleSubmit}
              >
                <p>Submit</p>
              </Button>
            </div>
          }
        />
        <CheckoutComplete
          className="blue-popup-backdrop"
          isQuoteOnly
          upperText="Submission Successful!"
          midText="Thank you for your interest in partnering PolicyPal. We will be contacting you within 2 working days."
          buttonText="Confirm"
          wrappedComponentRef={node => this.checkoutComplete = node}
          callback={() => this.handleOnClose()}
        />
      </Fragment>
    );
  }
}

Enquiry.defaultProps = {
  isOpen: false,
  interest: '',
  onClose: null,
  // onSubmit: null,
};

Enquiry.propTypes = {
  isOpen: PropTypes.bool,
  interest: PropTypes.string,

  onClose: PropTypes.func,
  // onSubmit: PropTypes.func,
  // handleAPI: PropTypes.func.isRequired,
};

export default Enquiry;
