import React, { Component, Fragment } from 'react';
import { Col, Row } from 'reactstrap';
import InfoPage from 'components/Templates/InfoPage/InfoPage';
import Mouse from 'components/Rewards/Mouse';
import Button from 'components/FormsV3/Button/Button';
import Enquiry from './Enquiry';
import mainImg from 'assets/images/Partnership/main.svg';
import mapIcon from 'assets/images/Partnership/icon__map.svg';
import rewardIcon from 'assets/images/Partnership/icon__reward.svg';
import './Partnership.scss';

const FEATURES = [
  {
    icon: mapIcon,
    title: 'Strategy Partnership',
    desc: 'Everyone deserves a wholesome user experience. Join us as an API partner and direct your users to our portal in exchange for a small fee. Everybody wins!',
    buttonText: 'Apply Now',
    link: '',
  },
  {
    icon: rewardIcon,
    title: 'Rewards Partnership',
    desc: 'Elevate your brand to greater heights as a rewards partner. Rewards Marketplace is an in-app feature for PolicyPal users to redeem exclusive deals with $P credits.',
    buttonText: 'Apply Now',
    link: '',
  },
]

class Partnership extends Component {
  constructor(props) {
    super(props)

    this.state = {
      isOpenEnquiryModal: false,
      featureName: '',
    }

    this.handleOnClickApply = this.handleOnClickApply.bind(this);
    this.handleOnCloseEnquiryModal = this.handleOnCloseEnquiryModal.bind(this);
    this.renderFeature = this.renderFeature.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  handleOnCloseEnquiryModal() {
    const { isOpenEnquiryModal } = this.state;
    this.setState({
      isOpenEnquiryModal: !isOpenEnquiryModal,
      featureName: '',
    });
  }

  handleOnClickApply(featureName) {
    const { isOpenEnquiryModal } = this.state;
    this.setState({
      isOpenEnquiryModal: !isOpenEnquiryModal,
      featureName,
    })
  }

  renderFeature(data, index) {
    const { icon, title, desc, buttonText } = data;

    return (
      <Col key={`feature-${index}`} xs="12" sm={{offset: 2, size: 8}} md={{offset: 0, size: 6}}>
        <div className="feature">
          <img src={icon} alt={title} className="image" />
          <h5 className="title">{title}</h5>
          <p className="desc">{desc}</p>
          <Button
            className="button"
            variant="medium-flat-green"
            height={48}
            width={162}
            onClick={() => this.handleOnClickApply(title)}
          >
            <p>{buttonText}</p>
          </Button>
        </div>
      </Col>
    );
  }

  render() {
    const { isOpenEnquiryModal, featureName } = this.state;

    return (
      <Fragment>
        <InfoPage
          className="partnership-container info-page-margin"
          noPolicyPal
          subscribeOverwrite={<Fragment />}
          noAutoMargin
          mode="full"
        >
          <Col xs="12">
            <div className="why-partner">
              <Row className="align-items-center" noGutters>
                <Col xs="12" md="6" className="order-md-2 mb-5 mb-md-0">
                  <img src={mainImg} className="image" alt="Why partner?" />
                </Col>

                <Col xs="12" md="6" className="order-md-1">
                  <h1 className="title text-center text-md-left">Why Partner?</h1>
                  <p className="desc text-center text-md-left">We’re all ears for new collaborations. Have a big idea to share? We’d love to partner with you and benefit our 50,000 PolicyPal users. Let's work together to design long-term, innovative and integrated product solutions to better serve your business needs.</p>
                </Col>
              </Row>
            </div>
          </Col>

          <Col xs="12">
            <Mouse className="d-none d-md-block ml-auto mr-auto mt-2 mb-5" />
          </Col>

          <Col xs="12">
            <div className="features">
              <Row>
                { FEATURES.map(this.renderFeature) }
              </Row>
            </div>
          </Col>
        </InfoPage>

        <Enquiry
          isOpen={isOpenEnquiryModal}
          interest={featureName}
          onClose={() => this.handleOnCloseEnquiryModal()}
        />
      </Fragment>
    );
  }
}

Partnership.defaultProps = {
};

Partnership.propTypes = {
};

export default Partnership;
