import React, { Component } from 'react';
import PropTypes from 'prop-types';
import mouseImg from 'assets/images/FormLanding/Mouse.svg';
import './Mouse.scss';

class Mouse extends Component {
  constructor(props) {
    super(props);

    this.getClasses = this.getClasses.bind(this);
  }

  getClasses() {
    const { className } = this.props;
    const classes = ['mouse__wrapper'];

    if (className) {
      classes.push(className);
    }

    return classes.join(' ');
  }

  render() {
    return (
      <div className={this.getClasses()}>
        <img src={mouseImg} className="mouse__img" alt="Mouse" />
      </div>
    )
  }
}

Mouse.defaultProps = {
  className: '',
};

Mouse.propTypes = {
  className: PropTypes.string,
};

export default Mouse;